import React, { useState } from "react";
import { NavLink } from 'react-router-dom';
import './NavBar.css';

// Componente NavBar - Barra de navegação do site
const NavBar = () => {

  const [menuActive, setMenuActive] = useState(false);

  const toggleMenu = () => {
    setMenuActive(!menuActive);
  };

  return (
    <nav>
      <h1 className="titleprincipal">Maison Nordic</h1>

      <div className={`menu-hamburguer ${menuActive ? 'ativo' : ''}`} onClick={toggleMenu}>
        &#9776; {/* This is the hamburger icon */}
      </div>
      <ul className={menuActive ? 'ativo' : ''}>
        <li>
          <NavLink
            to="/"
            end
            className={({ isActive }) => isActive ? "menu-ativo" : ""} onClick={toggleMenu}
          >
            Home
          </NavLink>
        </li>
        <li>
          <NavLink
            to="/Sobre"
            className={({ isActive }) => isActive ? "menu-ativo" : ""} onClick={toggleMenu}
          >
            Nordic Roots
          </NavLink>
        </li>
        <li>
          <NavLink
            to="/Menu"
            className={({ isActive }) => isActive ? "menu-ativo" : ""} onClick={toggleMenu}
          >
            The Nordic Table
          </NavLink>
        </li>
        <li>
          <NavLink
            to="/Especialidades"
            className={({ isActive }) => isActive ? "menu-ativo" : ""} onClick={toggleMenu}
          >
            Chef's Picks
          </NavLink>
        </li>
        <li>
          <NavLink
            to="/MenuEmGrupo"
            className={({ isActive }) => isActive ? "menu-ativo" : ""} onClick={toggleMenu}
          >
            Shared Table
          </NavLink>
        </li>
        <li>
          <NavLink
            to="/Reservas"
            className={({ isActive }) => isActive ? "menu-ativo" : ""} onClick={toggleMenu}
          >
            Nordic Welcome
          </NavLink>
        </li>
      </ul>
    </nav>
  );
};

export default NavBar;
