import React, { useState, useEffect} from "react";
import "./GestaoCookies.css";

function CookieBanner() {
    const [isVisible, setIsVisible] = useState(false);

    useEffect(() => {
        const hasCookie = localStorage.getItem("maison_nordic_cookie");
        if (!hasCookie) {
            setIsVisible(true);
        }
    }, []);

    const handleAccept = () => {
        localStorage.setItem("maison_nordic_cookie", "true");
        setIsVisible(false);
    };

    if(!isVisible) {
        return null;
    }

    return (
        <div className="bannerStyle">
        <div className="contentStyle">
          <p>
            We use cookies to ensure you get the best experience on our website. By continuing
            to use our site, you accept our cookie policy.
          </p>
          <button className="buttonStyle" onClick={handleAccept}>
            Accept
          </button>
        </div>
      </div>
    );
}

export default CookieBanner;